export function useFormRules () {
  return {
    name: [
      {
        required: true,
        message: '',
        trigger: 'blur'
      },
      {
        min: 1,
        max: 7,
        message: '17',
        trigger: 'change'
      }
    ],
    intro: [
      {
        required: true,
        message: '',
        trigger: 'blur'
      },
      {
        min: 3,
        max: 100,
        message: '3',
        trigger: 'change'
      }
    ],
    email: [
      {
        required: true,
        message: '',
        trigger: 'blur'
      },
      {
        pattern: /^[a-zA-Z0-9-_]+@[a-zA-Z0-9]+(\.[a-zA-Z0-9-_]+)+$/,
        message: '',
        trigger: 'change'
      }
    ]
  }
}
