// 
// data
export const login = (user) => {
  return requestNet({
    method: 'POST',
    url: '/mp/v1_0/authorizations',
    data: user
  })
}
// 
// token()
export const getUserProfile = (userToken) => {
  return requestNet({
    method: 'GET',
    url: '/mp/v1_0/user/profile'
  })
}

// 
export const updateUserphoto = (data) => {
  return requestNet({
    method: 'PATCH',
    url: '/mp/v1_0/user/photo',
    data
  })
}

// 
export const updateUser = data => {
  return requestNet({
    method: 'PATCH',
    url: '/mp/v1_0/user/profile',
    data
  })
}
