<template>
  <div class="settings-container">
    <a-card>
      <template #title>
        <a-breadcrumb separator=">">
          <a-breadcrumb-item>
            <router-link :to="{name: 'Home'}"></router-link>
          </a-breadcrumb-item>
          <a-breadcrumb-item></a-breadcrumb-item>
        </a-breadcrumb>
      </template>
      <Suspense>
        <template #default>
          <a-spin :spinning="spinning">
            <a-row>
              <a-col :span="16">
                <a-form ref="formCom"
                        :model="userProfile"
                        :rules="userFormRules"
                        :label-col="labelCol"
                        :wrapper-col="wrapperCol">
                  <a-form-item label="">
                    <span>{{ userProfile.id }}</span>
                  </a-form-item>
                  <a-form-item label="">
                    {{ userProfile.mobile }}
                  </a-form-item>
                  <a-form-item name="name" label="">
                    <a-input v-model:value="userProfile.name" allow-clear>
                      <template #prefix>
                        <user-outlined type="user"/>
                      </template>
                    </a-input>
                  </a-form-item>
                  <a-form-item name="intro" label="">
                    <a-textarea v-model:value="userProfile.intro" allow-clear/>
                  </a-form-item>
                  <a-form-item name="email" label="">
                    <a-input v-model:value="userProfile.email" allow-clear/>
                  </a-form-item>
                  <a-form-item :wrapper-col="{ span: 16, offset: 4 }">
                    <a-button type="primary" @click="onUpdateUser"
                              :disabled="spinning">
                    </a-button>
                  </a-form-item>
                </a-form>
              </a-col>
              <a-col :span="8">
                <!--refinputinput-->
                <div class="avatar-image" @click="file.click()">
                  <a-avatar shape="square"
                            :size="{ xs: 80, sm: 120, md: 160, lg: 200, xl: 240, xxl: 280 }">
                    <template #icon>
                      <img :src="userProfile.photo" alt="">
                    </template>
                  </a-avatar>
                  <p class="avatar-desc"></p>
                  <input type="file"
                         hidden
                         ref="file"
                         @change="onFileChange(onDialogOpened)"
                  >
                  <a-modal
                    title=""
                    v-model:visible="visible"
                    :confirm-loading="confirmLoading"
                    okText=""
                    cancel-text=""
                    :afterClose="onDialogClosed"
                    @ok="handleOk"
                  >
                    <div class="preview-image">
                      <img :src="previewImage" alt="" ref="image">
                    </div>
                  </a-modal>
                </div>
              </a-col>
            </a-row>

          </a-spin>
        </template>
      </Suspense>
    </a-card>
  </div>
</template>

<script>
import { UserOutlined } from '@ant-design/icons-vue'
import { message } from 'ant-design-vue'
import { getUserProfile, updateUserphoto, updateUser } from '@/api/user'
import { useFormRules } from '@/components/Settings/useSettingsValidate'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import { Suspense, ref, onBeforeMount } from 'vue'

export default {
  name: 'SettingsIndex',
  components: {
    Suspense,
    UserOutlined
  },
  setup (props, context) {
    // 
    const spinning = ref(false)

    // 
    const userFormRules = useFormRules()

    // 
    const formCom = ref()

    // 
    const file = ref()
    // 
    const visible = ref(false)
    // 
    const previewImage = ref('')
    // 
    const image = ref()
    // 
    const confirmLoading = ref(false)
    // 
    const onFileChange = function (callback) {
      // 
      // file.valuerefinputfiles
      // URLURL.createObjectURLurlurlurl
      // urlblob:http://xxxxxxx
      previewImage.value = window.URL.createObjectURL(file.value.files[0])
      // change
      file.value.value = ''
      // 
      visible.value = true
      // 
      // 
      // 
      callback()
    }
    // 
    // 
    let cropper
    const onDialogOpened = function () {
      // 
      setTimeout(() => {
        // 
        // .replace
        // 
        cropper = new Cropper(image.value, {
          // 
          aspectRatio: 1,
          // 
          viewMode: 1,
          // 
          dragMode: 'none',
          // 
          background: false,
          // 
          cropBoxResizable: false
        })
      }, 500)
    }
    // 
    const onDialogClosed = function () {
      // 
      cropper.destroy()
    }
    // 
    const handleOk = async function () {
      confirmLoading.value = true
      // 
      cropper.getCroppedCanvas().toBlob(file => {
        // formData
        const fd = new FormData()
        // photo
        fd.append('photo', file)
        // 
        updateUserphoto(fd).then(res => {
          if (res.status === 201) {
            message.success('')
          } else {
            message.error('')
          }
          confirmLoading.value = false
          visible.value = false
          loadUserProfile()
          // 
          context.emit('updateUserProfile')
        })
      })
    }

    // 
    const userProfile = ref({})
    const loadUserProfile = async () => {
      spinning.value = true
      const res = await getUserProfile(window.localStorage.getItem('user'))
      userProfile.value = res.data.data
      spinning.value = false
    }

    // 
    const patchUser = async function () {
      spinning.value = true
      const {
        name,
        intro,
        email
      } = userProfile.value
      await updateUser({
        name,
        intro,
        email
      })
      spinning.value = false
    }

    // 
    const onUpdateUser = async function () {
      // 
      formCom.value.validate().then(() => {
        console.log('')
        patchUser()
        // 
        context.emit('updateUserProfile')
      }).catch(error => {
        console.log(` ${error}`)
      })
    }
    onBeforeMount(async () => {
      await loadUserProfile()
    })
    return {
      labelCol: {
        style: {
          width: '100px'
        }
      },
      wrapperCol: {
        span: 16
      },
      spinning,
      file,
      visible,
      confirmLoading,
      previewImage,
      image,
      onDialogOpened,
      onDialogClosed,
      onFileChange,
      handleOk,
      formCom,
      userFormRules,
      userProfile,
      onUpdateUser
    }
  }
}
</script>

<style lang="less" scoped>
.settings-container {
  padding: 10px;

  .avatar-image {
    cursor: pointer;

    .avatar-desc {
      margin-top: 20px;
      text-indent: 60px;
    }
  }

}

.preview-image {
  img {
    display: block;
    max-width: 100%;
    height: 250px
  }
}
</style>
